@import "../../inc/vars";
.acrobits_customize {
	padding: 72px 0 20px;
	position: relative;
	z-index: 2;
	&:before {
		content: '';
		display: block;
		border-radius: 1084px;
		background: rgba(80, 50, 139, 0.06);
		filter: blur(170px);
		position: absolute;
		width: 1084px;
		height: 976px;
		right: -30%;
		bottom: 0;
	}
	@media (min-width: $screen-sm){
		padding: 120px 0 70px;
	}
	.title-box {
		padding: 0 30px 20px;
		text-align: center;
		@media (min-width: $screen-sm){
			padding: 0 30px 70px;
		}
	}
	&__title {
		@include titleGrad();
		@include pop(400, 35, 1.24);
		letter-spacing: -0.7px;
		margin-bottom: 24px;
		@media (min-width: $screen-sm){
			@include pop(400, 44, 1.12);
			letter-spacing: -0.88px;
			margin-bottom: 16px;
		}
	}
	&__description {
		@include fig(400, 20, 1.24);
		color: #4F4B58;
		text-align: center;
		@media (min-width: $screen-sm){
			line-height: 156%;
		}
	}
	&__slides {
		display: flex;
		justify-content: center;
		flex-wrap: wrap;
		position: relative;
		&:before {
			content: '';
			position: absolute;
			display: block;
			width: 400px;
			height: 350px;
			border-radius: 14.964px;
			opacity: 0.6;
			background: var(--purple-purple-600, #543695);
			filter: blur(93.52574157714844px);
			left: 50%;
			top: 50%;
			transform: translate(-50%, -50%);
			z-index: 2;
		}
		.slide {
			position: relative;
			@media (min-width: $screen-sm){
				max-width: 50%;
			}
		}
		.slide-image {
			img {
				width: 694px;
				box-shadow: 0px 0px 30px rgba(0, 0, 0, 0.3);
				border-radius: 15px;
				max-width: 100%;
				height: auto;
			}
		}
		.slide-logo {
			padding: 0 30px;
			margin: 20px 0;
			display: flex;
			align-items: center;
			@include pop(400, 13, 1.24);
			color: #515354;
			img {
				margin: 0 15px 0 0;
				@media (min-width: $screen-sm){
					margin: 0 20px 0 0;
				}
			}
			@media (min-width: $screen-sm){
				padding: 0 70px;
				@include pop(400, 17, 1.24);
			}
		}
		.slide1 {
			bottom: -70px;
			animation: pulse 8s infinite;
			display: flex;
			flex-direction: column;
			@media (min-width: $screen-sm){
				right: -120px;
				bottom: auto;
			}
			img {
				animation: grayscale 8s infinite;
			}
			.slide-logo {
				order: -1;
				@media (min-width: $screen-sm){
					order: 2;
				}
			}
		}
		.slide2 {
			top: -70px;
			animation: pulse 8s infinite;
			animation-delay: 4s;
			@media (min-width: $screen-sm){
				left: -120px;
				top: auto;
			}
			img {
				animation: grayscale 8s infinite;
				animation-delay: 4s;
			}
			.slide-logo {
				justify-content: flex-end;
				img {
					margin: 0 0 0 10px;
					width: 60px;
					@media (min-width: $screen-sm){
						margin: 0 0 0 20px;
						width: auto;
					}
				}
			}
		}
	}
}

@keyframes pulse {
	0% {
		transform: scale(0.95) translate(-30px, 30px);
		opacity: 0.8;
		z-index: 1;
	}
	20% {
		transform: scale(1);
		z-index: 2;
		opacity: 1;
	}
	50% {
		transform: scale(1);
		z-index: 2;
		opacity: 1;
	}
	70% {
		transform: scale(0.95) translate(-30px, 30px);
		opacity: 0.8;
		z-index: 1;
	}
	100% {
		transform: scale(0.95) translate(-30px, 30px);
		opacity: 0.8;
		z-index: 1;
	}
}

@keyframes grayscale {
	0% {
		filter: grayscale(1);
	}
	20% {
		filter: grayscale(0);
	}
	50% {
		filter: grayscale(0);
	}
	70% {
		filter: grayscale(1);
	}
	100% {
		filter: grayscale(1);
	}
}